import { Grid } from "@mui/material";
import React from "react";
import './Signup.css'
import insta_logo from "../../images/logoinsta.png"
import { Link } from "react-router-dom";
import { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";

function SignUp(){
    const navigate = useNavigate();
    const [formData, setData] = useState({});
    function handleSubmit(event){
        event.preventDefault();
        axios.post("http://localhost:8080/posts", formData).then(res => {
            console.log(res);
           navigate('/');
            
        }).catch(err => console.log(err));
    }

    function handleChange(event){
        
        setData({...formData, [event.target.name]: event.target.value})
    }

        
        return(
                <div>
                <Grid container>
                    <Grid item xs = {4}>

                    </Grid>
                    <Grid item xs = {5}>
                    <div className="signUpForm">
                                <img src={insta_logo} className="loginpage_logo" alt=""/>
                                
                                <div className="fbSignUp">
                                    <div>
                                        <h3>Sign up to see photos and videos from your friends.</h3>
                                        <button className="signuppage_button" type="">Log in with facebook</button>
                                    </div>
                                </div>
                                <div className="login_ordiv">
                                    <div className="login_divide"></div>
                                    <div className="login_or">OR</div>
                                    <div className="login_divide"></div>
                                </div>
                                <div className="loginpage_signin">
                                    <form onSubmit={handleSubmit} action="">
                                        <input name="mobile" className="signuppage_text" type="text" placeholder="Mobile or Email" onChange={handleChange}/>
                                        <input name="name" className="signuppage_text" type="text" placeholder="Full Name" onChange={handleChange}/>
                                        <input name="username" className="signuppage_text" type="text" placeholder="Username" onChange={handleChange}/>
                                        <input name="password" className="signuppage_text" type="password" placeholder=" Password" onChange={handleChange}/>
                                        <button className="signuppage_button" type="submit">Sign Up</button>
                                    </form>
                                </div>

                                <div>
                                    <span>Have an account ? </span>
                                    <Link to="/">Login</Link>
                                </div>
                                
                        </div> 
                    </Grid>
                    <Grid item xs = {3}>
                        
                    </Grid>
                </Grid>
            
            </div>
            
        );
}

export default SignUp