
import React from "react";
import { Grid } from "@mui/material";
import Sidebar from "./sidebar";
import Navbar from "./navbar";
import './Mainpage.css'
import AccountCircleIcon from "./accountCircle";
import Post from "./post";
import MessageSection from "./messageSec";
function Mainpage(){

    return(
        <div>

            <Grid container>
                <Grid item xs = {1}>
                    <Sidebar/>
                </Grid>

                <Grid item xs = {7}>
                    <div className="navBar">
                        <div style={{display:'flex'}}>
                            <Navbar />
                            <Navbar />
                            <Navbar />
                            <Navbar />
                            <Navbar />
                            <Navbar />
                            <Navbar />
                            <Navbar />
                        </div>

                        <div>
                            <Post/>
                            <Post/>
                            <Post/>
                            <Post/>
                        </div>

                    </div>
                    
                    

                </Grid>
                <Grid item xs={4}>
                    {/* <div className="messageSection">
                        <AccountCircleIcon/>
                                
                    </div> */}
                    <div>
                        <MessageSection/>
                        <MessageSection/>
                        <MessageSection/>
                        <MessageSection/>
                        <MessageSection/>
                        <MessageSection/>
                    </div>
                </Grid>
            </Grid>



        </div>
    )

}
export default Mainpage;