import React from "react";
import './Mainpage.css'
import AccountCircleIcon from "./accountCircle";
import postImg from '../../images/OMEN.jpg'
import likeImg from '../../images/love.svg'
import shareImg from '../../images/share.svg'
import saveImg from '../../images/save.svg'
import commentImg from '../../images/comment.svg'
function Post(){

    return(
        <div className="post">
            <div className="userName">
                <AccountCircleIcon/>
                                    
            </div>
            <div className="content">
                <img className="postImage" src={postImg} alt="" />
            </div>
            <div style={{display:'flex', margin:'20px'}}>
                
                <img src={likeImg} width="35px" height="35px" alt="" />
                <img src={commentImg} width="35px" height="35px" alt="" />
                <img src={shareImg} width="350px" height="35px" alt="" />
                <img style={{float:'right'}} src={saveImg}  width="35px" height="35px" alt="" />
                
            </div>
            
        </div>
    );
}
export default Post