import React, {Component} from "react";
import { Grid } from "@mui/material";
import insta_image from "../../images/9364675fb26a.svg" 
import insta_logo from "../../images/logoinsta.png"
import fb from '../../images/fb.png';
import './LoginPage.css'
import { Link } from "react-router-dom";
import axios from "axios";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useEffect } from "react";


const LoginPage = () =>{


    const navigate = useNavigate();

    let [username, setUserName] = useState("");
    let [password, setPassword] = useState("");

    const handleUserName=(e)=>{
        setUserName(e.target.value)
    }

    const handlePassword = (e) =>{
        setPassword(e.target.value)
    }

    

    

    function handleSubmit(event){
        event.preventDefault();
        console.log(username, password);
        axios.get("http://localhost:8080/posts/",).then((res)=>{
            console.log(res.data);
            let arr = res.data;
            let res1 = arr.filter((ele, index, arr)=>{
                if(ele.username == username && ele.password == password){
                    return ele;
                }
            })
            if(res1.length == 0){
                alert("Invalid Credentials");
                navigate('/');
            }else{
                console.log(res1);
                localStorage.setItem('username', res1[0].username)
                alert(res1[0].name)
                navigate('/main');
            }
    
            //localStorage.setItem('token', res.data.token);
            
        }).catch((err)=>{
            console.log(err);
            alert("Service Error");
        })
        
        
    }

    


    

    return (
        <div>
        <Grid container>
            <Grid item sm={3}>
                
            </Grid>

            <Grid item sm={6}>
                <div className="loginpage_main">
                    <div>
                       <img src={insta_image} height={450} width={300} alt=""/>
                    </div>
                    <div className="loginpage_rightcomponent">
                        <img src={insta_logo} className="loginpage_logo" alt=""/>
                        <div className="loginpage_signin">
                                <form onSubmit={handleSubmit}>
                                    <input name="username"  className="loginpage_text" type="text" placeholder=" Username, Email, Phone" onChange={(e)=>handleUserName(e)}/>
                                    <input name="password"  className="loginpage_text" type="password" placeholder=" Password" onChange={handlePassword} />
                                    <button type="submit" className="loginpage_button">Log in</button>
                                </form>
                        </div>
                        <div className="login_ordiv">
                            <div className="login_divide"></div>
                            <div className="login_or">OR</div>
                            <div className="login_divide"></div>
                        </div>
                        <div className="login__fb">
                            <img src={fb} width="15px" style={{ "marginRight":"5px" }} alt=""/>Log in with Facebook
                        </div>
                        <div className="login_forgt"> Forgot password?</div> 
                        <div className="loginpage__signupoption">
                            <div className="loginpage__signup" style={{"marginTop": "10px"}}>
                                <span className="signin">Don't have an account? </span>
                                
                            </div>    
                            <Link to="/signup">
                                    SignUp
                            </Link>               
                        </div> 
                    </div>
                    
                </div>

                
                
            </Grid>

            <Grid item sm={3}>
                
            </Grid>
        </Grid>
    </div>

    )
}
export default LoginPage